import { all, takeEvery, fork, call, put } from 'redux-saga/effects';
import actions from './actions';
import {
  getCategoriesReq,
  getColorsReq,
  getProductsReq
} from "./service";

export function* getCategories() {
  yield takeEvery(actions.GET_CATEGORIES, function*({ eventId, locale }) {
    try {

    } catch (error) {
      console.error('getCategories error id: ' + eventId + ' ', error);
    }
  });
}

export function* getColors() {
  yield takeEvery(actions.GET_COLORS, function*() {
    try {

    } catch (error) {
      console.error('getColors error ', error);
    }
  });
}

export function* getProduct() {
  yield takeEvery(actions.GET_PRODUCT, function*({ products, productId }) {
    try {
      console.log('getProduct ', products, productId);
    } catch (error) {
      console.error('getProduct error ', error);
    }
  });
}

export function* getProducts() {
  yield takeEvery(actions.GET_PRODUCTS, function*() {
    try {
      const products = yield call(getProductsReq);
      yield put({ type: actions.PUT_PRODUCTS, products });
    } catch (error) {
      console.error('getProducts error ', error);
    }
  });
}


export default function* rootSaga() {
  yield all([
    fork(getCategories),
    fork(getColors),
    fork(getProduct),
    fork(getProducts)
  ]);
}



