import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import Clock from 'react-clock';
import Gallery from 'react-grid-gallery';
import actions from '../../actions';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ArrowBack from '@material-ui/icons/ArrowBack';

import { ProductWrapper } from './product.style';

const { getProduct } = actions;

class Product extends React.Component {
  constructor(props) {
    super(props);

    if ( props.match.params['id'] ) {
      props.getProduct(props.match.params['id']);
    }
  }


  render() {
    const { product } = this.props;

    if (!product) {
      return null;
    }

    return (
      <ProductWrapper>
        <Grid className="contentWrapper" container spacing={24}>
          <Grid id="productContent" item xs={12} >
            <Grid container spacing={24}>
              <Grid className="productContentLeft" item xs={12} sm={8}>
                <Link to="/" className="backButton">
                  <Button>
                    <span><ArrowBack /></span>
                    <span>back to home</span>
                  </Button>
                </Link>
                <ul>
                  <li>
                    <h3>{product.name}</h3>
                  </li>
                  <li>
                    <span>{product.description}</span>
                  </li>
                  <li>
                    <span>{product.colors}</span>
                  </li>
                  <li>
                    <span>{product.categories}</span>
                  </li>
                  <li>
                    <span>{product.prices}</span>
                  </li>
                </ul>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </ProductWrapper>
    );
  }
}

function mapStateToProps(state) {
  return {
    product: state.product
  };
}

export default connect(mapStateToProps, {
  getProduct
})(Product);



